<template>
  <div id="myContact" class="w_h">
    <div class="open flex_column w_h">
      <img
        class="head"
        src="../assets/img/icon/head.png"
        alt=""
        @click="headClick"
      />
      <span class="name">钟芙林</span>
      <div class="flex_row contact">
        <el-popover
          placement="top"
          title=""
          width="200"
          trigger="hover"
          content=""
        >
          <img class="w_h" src="../assets/img/contact/QQ.png" alt="" />
          <img slot="reference" src="../assets/img/icon/QQ.svg" alt="" />
        </el-popover>
        <el-popover
          placement="top"
          title=""
          width="200"
          trigger="hover"
          content=""
        >
          <img class="w_h" src="../assets/img/contact/WX.png" alt="" />
          <img slot="reference" src="../assets/img/icon/WX.svg" alt="" />
        </el-popover>
        <img
          @click="toPage('github')"
          src="../assets/img/icon/github.svg"
          alt=""
        />
        <img @click="toPage('csdn')" src="../assets/img/icon/csdn.svg" alt="" />
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/css/head.css";
export default {
  name: "",
  data() {
    return {};
  },
  watch: {},
  created() {},
  methods: {
    toPage(val) {
      if (val == "github") {
        window.open("https://github.com/fulin-z", "_blank");
      } else {
        window.open("https://blog.csdn.net/qq_43482627?type=blog", "_blank");
      }
    },
    headClick(e) {
      let x = e.clientX;
      let y = e.clientY;
      let span = document.createElement("span");
      span.style.left = x + "px";
      span.style.top = y + "px";
      span.setAttribute("class", "anim");
      document.querySelectorAll("#myContact div")[1].appendChild(span);
      setTimeout(() => {
        span.remove();
      }, 1000);
    },
  },
};
</script>

<style scoped>
#myContact {
  position: relative;
  overflow: hidden;
}
.open {
  background: linear-gradient(to left, #0a3838, #144949, #125f5f);
  align-items: center;
  justify-content: center;
}
.head {
  width: 80px;
  height: 80px;
  border-radius: 100%;
  user-select: none;
}
.head:hover {
  cursor: pointer;
  box-shadow: 0px 0px 10px 1px #919191;
}
.head:active {
  box-shadow: none;
}
.name {
  color: #c7c7c7;
  padding: 1rem 0;
  font-weight: bold;
  letter-spacing: 2px;
  user-select: none;
}
.contact img {
  height: 30px;
  width: 30px;
  user-select: none;
  margin: 0 5px;
}
.contact img:hover {
  cursor: pointer;
}
</style>