<template>
  <div id="myCarousel" class="w_h">
    <el-carousel
      class="w_h"
      direction="vertical"
      :autoplay="false"
      :loop="false"
      ref="carousel"
    >
      <el-carousel-item>
        <myContact></myContact>
      </el-carousel-item>
      <el-carousel-item>
        <myInfo></myInfo>
      </el-carousel-item>
      <el-carousel-item>
        <mySkills></mySkills>
      </el-carousel-item>
      <el-carousel-item>
        <myWork></myWork>
      </el-carousel-item>
      <el-carousel-item>
        <myProject></myProject>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import myContact from "./myContact.vue";
import myInfo from "./myInfo.vue";
import mySkills from "./mySkills.vue";
import myWork from "./myWork.vue";
import myProject from "./myProject.vue";
export default {
  name: "",
  components: {
    myContact,
    myInfo,
    mySkills,
    myWork,
    myProject,
  },
  data() {
    return {
      timeOut: null,
    };
  },
  watch: {},
  created() {
    window.addEventListener("mousewheel", this.rollScroll);
  },
  beforeDestroy() {
    window.removeEventListener("mousewheel", this.rollScroll);
  },
  methods: {
    rollScroll(event) {
      let _that = this;
      // chrome、ie使用的wheelDelta，火狐使用detail
      let scrollVal = event.wheelDelta || event.detail;
      // 节流
      if (!_that.timeOut) {
        _that.timeOut = setTimeout(() => {
          _that.timeOut = null;
          scrollVal > 0
            ? _that.$refs.carousel.prev()
            : _that.$refs.carousel.next();
        }, 300);
      } else {
      }
    },
  },
};
</script>

<style scoped>
#myCarousel >>> .el-carousel__container {
  height: 100%;
}
</style>